/* -------------------------------- 

File#: _1_menu
Title: Menu
Descr: Application menu that provides access to a set of functionalities
Usage: codyhouse.co/license

-------------------------------- */
.menu {
  --menu-vertical-gap: 4px;
  --menu-item-padding: var(--space-xxs) var(--space-sm);
  list-style: none;
  width: 220px;
  position: fixed;
  background-color: var(--color-bg);
  box-shadow: var(--shadow-md);
  padding: var(--space-xxs) 0;
  border-radius: var(--radius-md);
  z-index: var(--zindex-popover);
  user-select: none;
  margin-top: var(--menu-vertical-gap);
  margin-bottom: var(--menu-vertical-gap);
  overflow: auto;
  --space-unit: 1rem;
  --space-xxxxs: calc(0.125 * 1rem);
  --space-xxxs: calc(0.25 * 1rem);
  --space-xxs: calc(0.375 * 1rem);
  --space-xs: calc(0.5 * 1rem);
  --space-sm: calc(0.75 * 1rem);
  --space-md: calc(1.25 * 1rem);
  --space-lg: calc(2 * 1rem);
  --space-xl: calc(3.25 * 1rem);
  --space-xxl: calc(5.25 * 1rem);
  --space-xxxl: calc(8.5 * 1rem);
  --space-xxxxl: calc(13.75 * 1rem);
  --component-padding: var(--space-md);
  --text-xs: calc((1rem / 1.2) / 1.2);
  --text-sm: calc(var(--text-xs) * 1.2);
  --text-md: calc(var(--text-sm) * 1.2 * 1.2);
  --text-lg: calc(var(--text-md) * 1.2);
  --text-xl: calc(var(--text-lg) * 1.2);
  --text-xxl: calc(var(--text-xl) * 1.2);
  --text-xxxl: calc(var(--text-xxl) * 1.2);
  --text-xxxxl: calc(var(--text-xxxl) * 1.2);
  font-size: 1rem;
  visibility: hidden;
  opacity: 0;
  transition: visibility 0s 0.2s, opacity 0.2s;
}

.menu--is-visible {
  visibility: visible;
  opacity: 1;
  transition: visibility 0s, opacity 0.2s;
}

.menu--overlay {
  z-index: var(--zindex-overlay);
}

.menu__content {
  display: block;
  display: flex;
  align-items: center;
  text-decoration: none;
  padding: var(--menu-item-padding);
  color: var(--color-contrast-high);
  cursor: pointer;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.menu__content:hover {
  background-color: var(--color-contrast-lower);
}
.menu__content:focus {
  outline: none;
  background-color: hsla(var(--color-primary-h), var(--color-primary-s), var(--color-primary-l), 0.1);
}

.menu__label {
  padding: var(--menu-item-padding);
  font-size: var(--text-sm);
  color: var(--color-contrast-medium);
}

.menu__separator {
  height: 1px;
  background-color: var(--color-contrast-low);
  margin: var(--menu-item-padding);
}

.menu__icon {
  color: hsla(var(--color-contrast-higher-h), var(--color-contrast-higher-s), var(--color-contrast-higher-l), 0.7);
  margin-right: var(--space-xxs);
}

/* -------------------------------- 

File#: _2_menu-bar
Title: Menu Bar
Descr: Application menu with a list of common actions that users can perform
Usage: codyhouse.co/license

-------------------------------- */
:root {
  --menu-bar-button-size: 2.5em;
  --menu-bar-icon-size: 1em;
  --menu-bar-horizontal-gap: var(--space-xxs);
  --menu-bar-vertical-gap: 4px;
  --menu-bar-label-size: var(--text-xs);
}

.menu-bar {
  list-style: none;
  display: inline-flex;
  align-items: center;
}

.menu-bar__item {
  position: relative;
  display: inline-block;
  display: flex;
  align-items: center;
  justify-content: center;
  height: var(--menu-bar-button-size);
  width: var(--menu-bar-button-size);
  border-radius: 50%;
  cursor: pointer;
  transition: background-color 0.2s;
}
.menu-bar__item:not(:last-child) {
  margin-right: var(--menu-bar-horizontal-gap);
}
.menu-bar__item:hover, .menu-bar__item.menu-control--active {
  background-color: var(--color-contrast-lower);
}
.menu-bar__item:hover > .menu-bar__icon, .menu-bar__item.menu-control--active > .menu-bar__icon {
  color: var(--color-contrast-higher);
}
.menu-bar__item:hover > .menu-bar__label, .menu-bar__item.menu-control--active > .menu-bar__label {
  clip: auto;
  clip-path: none;
  height: auto;
  width: auto;
}
.menu-bar__item:focus {
  outline: none;
  background-color: hsla(var(--color-primary-h), var(--color-primary-s), var(--color-primary-l), 0.1);
}
.menu-bar__item:active {
  background-color: var(--color-contrast-low);
}
.menu-bar__item:focus:active {
  background-color: hsla(var(--color-primary-h), var(--color-primary-s), var(--color-primary-l), 0.2);
}

.menu-bar__item--trigger {
  display: none;
}

.menu-bar__icon {
  display: block;
  color: var(--color-contrast-high);
  font-size: var(--menu-bar-icon-size);
  transition: color 0.2s;
}

.menu-bar__label {
  position: absolute;
  clip: rect(1px, 1px, 1px, 1px);
  clip-path: inset(50%);
  width: 1px;
  height: 1px;
  overflow: hidden;
  white-space: nowrap;
  top: 100%;
  left: 50%;
  transform: translateX(-50%) translateY(var(--menu-bar-vertical-gap));
  padding: var(--space-xxs) var(--space-xs);
  color: var(--color-bg);
  background-color: hsla(var(--color-contrast-higher-h), var(--color-contrast-higher-s), var(--color-contrast-higher-l), 0.95);
  border-radius: var(--radius-md);
  font-size: var(--menu-bar-label-size);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  pointer-events: none;
  user-select: none;
}

.menu-bar--collapsed .menu-bar__item--hide {
  display: none;
}
.menu-bar--collapsed .menu-bar__item--trigger {
  display: inline-block;
  display: flex;
}

.js .menu-bar {
  opacity: 0;
}
.js .menu-bar::before {
  display: none;
  content: "collapsed";
}
.js .menu-bar--loaded {
  opacity: 1;
}
@media (min-width: 32rem) {
  .js .menu-bar--expanded\@xs::before {
    content: "expanded";
  }
}
@media (min-width: 48rem) {
  .js .menu-bar--expanded\@sm::before {
    content: "expanded";
  }
}
@media (min-width: 64rem) {
  .js .menu-bar--expanded\@md::before {
    content: "expanded";
  }
}
@media (min-width: 80rem) {
  .js .menu-bar--expanded\@lg::before {
    content: "expanded";
  }
}
